import React, { useState, useEffect } from 'react';
import StepCard from './StepCard';
import STEP1 from "../../assets/HomePage/STEPS-01.jpg";
import STEP2 from "../../assets/HomePage/STEPS-02.jpg"
import STEP3 from "../../assets/HomePage/STEPS-03.jpg"
import STEP4 from "../../assets/HomePage/STEPS-04.jpg"
import STEP5 from "../../assets/HomePage/STEPS-05.jpg"
import STEP6 from "../../assets/HomePage/STEPS-06.jpg"
import './Step.css';

const SixStep = () => {
	return (
		<div class='stepcard'>
			<div class='free1'>
				<StepCard
					img={STEP1}
					to='s0'
					text="Emotional Awareness"
				/>
			</div>
			<div class='free2'>
				<StepCard
					img={STEP2}
					to='s1'
					text="Uncertainty: Worry And Anxiety"
				/>
			</div>
			<div class='free1'>
				<StepCard
					img={STEP3}
					to='s2'
					text="LEARNING TO SELF SOOTHE"
				/>
			</div>
			<div class='free2'>
				<StepCard
					img={STEP4}
					to='s3'
					text="CHANGING UNHEALTHY COPING BEHAVIOURS"
				/>
			</div>
			<div class='free1'>
				<StepCard
					img={STEP5}
					to='s4'
					text="SLEEP BETTER"
				/>
			</div>
			<div class='free2'>
				<StepCard
					img={STEP6}
					to='s0'
					text="Your Personal Plan"
				/>
			</div>
		</div>
	);
};

export default SixStep;
