import React from 'react';
import { Link } from 'react-router-dom';

const PNbutton = ({to,name}) => {
	return (
		<Link
			to={to}
			style={{ textDecoration: 'none', color: '#09386F' }}
		>
			<button className='button1'>
				<div
					style={{
						textDecoration: 'none',
						fontSize: '15px',
						color: '#09386F',
					}}
				>
					{name}
				</div>
			</button>
		</Link>
	);
};

export default PNbutton;
