import React, { Fragment, useEffect, useState } from 'react';
import Accordion from 'react-bootstrap/Accordion';
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import dayjs from 'dayjs';
import axios from 'axios';

const Form = ({ list }) => {
	const [responses, setResponses] = useState([]);
	const [date, setDate] = useState([]);
	const [r, setR] = useState(false);

	let values = {};

	const submitForm = async (e) => {
		e.preventDefault();
		for (let i = 1; i < 8; i++) {
			let q = 'q' + i;
			values[q] = document.getElementById(q).value;
		}
		const config = {
			headers: {
				'Content-Type': 'application/json',
			},
		};
		const body = JSON.stringify(values);
		const res = await axios.post(
			'https://mighty-bastion-04883.herokuapp.com/api/formsubmit',
			body,
			config
		);
		alert(res);
	};

	useEffect(async () => {
		if (!r) {
			let res = await axios.get(
				'https://mighty-bastion-04883.herokuapp.com/api/formsubmit'
			);
			const rev = [];
			const dates = [];
			for (let i = res.data.responses.length - 1; i >= 0; i--) {
				rev.push(res.data.responses[i]);
				dates.push(res.data.responses[i].date);
			}
			setResponses(rev);
			setDate(dates);
			setR(true);
		}
	}, []);
	return (
		<div>
			<form onSubmit={submitForm}>
				<div
					style={{
						display: 'flex',
						flexWrap: 'wrap',
						justifyContent: 'center',
					}}
				>
					{list.map((val, i) => {
						return (
							<div className={'div' + val.section.size + ' division'}>
								<p>{val.section.label}</p>
								<textarea
									className={val.section.size + ' divtxt'}
									rows='7'
									placeholder={val.section.placeholder}
									required
									id={'q' + (i + 1)}
								/>
							</div>
						);
					})}
				</div>
				<Button
					style={{
						display: 'block',
						marginLeft: 'auto',
						marginRight: 'auto',
						marginTop: '15px',
						marginBottom: '15px',
						border: 'none',
						background: '#4A6AD1',
						color: 'white',
						width: '100px',
						borderRadius: '5px',
					}}
					type='submit'
				>
					Save
				</Button>
			</form>
			<div style={{ height: '100px' }}></div>
			{r && (
				<Accordion
					defaultActiveKey='0'
					style={{ color: 'white', borderRadius: '15px' }}
				>
					{responses.reverse().map((val, i) => {
						return (
							<Card
								style={{ background: '#4A6AD1', cursor: 'pointer' }}
								key={i}
							>
								<Accordion.Toggle
									as={Card.Header}
									eventKey={i + 1}
									style={{ border: 'solid', borderColor: 'white' }}
								>
									Your Response of {dayjs(val.date).format('DD/MM/YYYY')}
								</Accordion.Toggle>
								<Accordion.Collapse eventKey={i + 1}>
									<Card.Body style={{ background: '#09386F' }}>
										<div className='query'>{val.q1}</div>
										<div className='answer'>{val.a1}</div>
										<div className='query'>{val.q2}</div>
										<div className='answer'>{val.a2}</div>
										<div className='query'>{val.q3}</div>
										<div className='answer'>{val.a3}</div>
										<div className='query'>{val.q4}</div>
										<div className='answer'>{val.a4}</div>
										<div className='query'>{val.q5}</div>
										<div className='answer'>{val.a5}</div>
										<div className='query'>{val.q6}</div>
										<div className='answer'>{val.a6}</div>
										<div className='query'>{val.q7}</div>
										<div className='answer'>{val.a7}</div>
									</Card.Body>
								</Accordion.Collapse>
							</Card>
						);
					})}
				</Accordion>
			)}
		</div>
	);
};

export default Form;
